
.container{
    margin-top: 150px;
    margin-left: 100px;
}

.slider-container{
    display: flex;
    justify-content: center;
    width: 1000px;
    height: 200px;
    align-items: center;
    position: relative;
}

.slider{
    border-radius: 1.5rem;
}

.slider-hidden{
    display: none;
}

.left-arrow,
.right-arrow{
    position: absolute;
    font-size: 35px;
    top: 50%;
    color: white;
    cursor: pointer;
}

.left-arrow{
    left: 20px;
}

.right-arrow{
    right: 0;
}

.indicators{
    display: flex;
    position: absolute;
    top: 100%;
    
}

.indicator{
    width: 8px;
    height: 8px;
    border-radius: 100%;
    border: white;
    margin: 0 2px;
    cursor: pointer;
}

.indicator-inactive {
    background-color: gray;
}
